.name-gen {
  background-color: #f5f5f5;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  max-width: 400px;
  display: flex;
  overflow: hidden;
}

.name-gen__result {
  padding: 8px;
}

.name-gen__result-race {
  text-align: right;
}

.name-gen__btn {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-image: linear-gradient(to right, #870000 0%, #190a05 51%, #870000 100%);
  background-size: 200%;
  border-radius: 10px;
  margin: 10px;
  padding: 15px 45px;
  transition: all .5s;
  display: block;
  box-shadow: 0 0 20px #eee;
}

.name-gen__btn:hover {
  color: #fff;
  background-position: 100%;
  text-decoration: none;
}
